<script>
export default {
  name:'Tags',
  data(){
    return{
      TagForm: {
        type: 2,
        tag: ''
      },
      rules: {
        tag: [
          { required: true, message: '', trigger: 'blur' },
        ]
      }
    }
  },
  methods:{
    handleTag(value,type) {
      this.$router.push({
        path:'searchresult',
        query:{
          tagValue:value.target.innerText,
          type
        }
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$router.push({
          path:'searchresult',
          query:{
            tagValue:this.TagForm.tag,
            type:this.TagForm.type
          }
      })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  },
}
</script>

