<script>
export default {
  name:'mTags',
  data(){
    return{
      TagForm: {
        type: 2,
        tag: ''
      },
      rules: {
        tag: [
          { required: true, message: '', trigger: 'blur' },
        ]
      }
    }
  },
  methods:{
    handleTag(value,type) {
      this.$router.push({
        path:'msearchresult',
        query:{
          tagValue:value.target.innerText,
          type
        }
      })
    },
  },
}
